<template>
  <el-container class="layout-container">
    <el-header>
      <div class="header-left">
        <el-button
          class="fold-button"
          text
          :icon="isCollapse ? DArrowRight : Menu"
          @click="isCollapse = !isCollapse"
        />
        <img src="@/assets/logo.png" alt="logo" class="logo">
        <el-icon class="title-icon"><School /></el-icon>
        <h2>导师双选系统-管理端</h2>
      </div>
      <div class="header-right">
        <el-dropdown trigger="click" @command="handleCommand">
          <span class="user-info">
            <template v-if="userStore.userInfo">
              {{ userStore.userInfo.adm_name }}
              <el-icon class="el-icon--right"><arrow-down /></el-icon>
            </template>
          </span>
          <template #dropdown>
            <el-dropdown-menu>
              <el-dropdown-item command="settings">设置</el-dropdown-item>
              <el-dropdown-item command="logout" divided>退出登录</el-dropdown-item>
            </el-dropdown-menu>
          </template>
        </el-dropdown>
      </div>
    </el-header>

    <el-container>
      <el-aside :width="isCollapse ? '64px' : '200px'" :class="{ 'is-collapse': isCollapse }">
        <el-menu
          :default-active="activeMenu"
          class="menu"
          router
          :collapse="isCollapse"
        >
          <el-menu-item index="/system-management/dashboard">
            <el-icon class="menu-icon"><HomeFilled /></el-icon>
            <span>首页</span>
          </el-menu-item>

          <el-sub-menu index="manage">
            <template #title>
              <el-icon class="menu-icon"><Management /></el-icon>
              <span>管理</span>
            </template>
            <el-menu-item index="/system-management/manage/student">
              <el-icon class="menu-icon"><UserFilled /></el-icon>
              <span>学生管理</span>
            </el-menu-item>
            <el-menu-item index="/system-management/manage/teacher">
              <el-icon class="menu-icon"><Avatar /></el-icon>
              <span>教师管理</span>
            </el-menu-item>
            <el-menu-item index="/system-management/manage/group">
              <el-icon class="menu-icon"><Connection /></el-icon>
              <span>小组管理</span>
            </el-menu-item>
          </el-sub-menu>

          <el-menu-item index="/system-management/config">
            <el-icon class="menu-icon"><Tools /></el-icon>
            <span>配置管理</span>
          </el-menu-item>

          <el-menu-item index="/system-management/settings">
            <el-icon class="menu-icon"><Setting /></el-icon>
            <span>设置</span>
          </el-menu-item>
        </el-menu>
      </el-aside>

      <el-main>
        <router-view v-slot="{ Component }">
          <component :is="Component" />
        </router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script setup>
import { computed, onMounted, ref } from 'vue'
import { useRouter, useRoute } from 'vue-router'
import { useUserStore } from '@/store/modules/user'
import { 
  Setting, 
  Management,
  HomeFilled,
  Avatar,
  UserFilled,
  ArrowDown,
  Connection,
  Tools,
  School,
  /* eslint-disable no-unused-vars */
  Menu,
  /* eslint-disable no-unused-vars */
  DArrowRight
  /* eslint-disable no-unused-vars */
} from '@element-plus/icons-vue'
import { getCookie } from '@/utils/cookie'

const router = useRouter()
const route = useRoute()
const userStore = useUserStore()

const activeMenu = computed(() => route.path)

const isCollapse = ref(false)

onMounted(() => {
  if (!userStore.userInfo) {
    const storedUserInfo = getCookie('userInfo')
    if (storedUserInfo) {
      userStore.userInfo = JSON.parse(storedUserInfo)
    } else {
      router.push('/system-management/auth')
    }
  }
})

const handleCommand = (command) => {
  switch (command) {
    case 'settings':
      router.push('/system-management/settings')
      break
    case 'logout':
      userStore.logout()
      router.push('/system-management/auth')
      break
  }
}
</script>

<style lang="scss" scoped>
@import '@/assets/styles/layout.scss';

:deep(.el-icon) {
  vertical-align: middle;
}
</style> 